import { useState } from "react";

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from "reactstrap";

import styled from "styled-components";

const RMNavbarDiv = styled.div`
  height: 10vh;
  padding: 0;
  margin: 0;
  background: transparent;
  color: white;

  .fa.fa-navicon {
    color: white;
  }

  a {
    color: white;
  }
`;

function RMNavbar() {
  const [toggle, setToggle] = useState(true);
  return (
    <RMNavbarDiv>
      <Navbar expand="md" className="justify-content-center">
        <NavbarToggler
          className="custom-toggler"
          onClick={() => setToggle(!toggle)}
        >
          <i className="fa fa-navicon" />
        </NavbarToggler>
        <Collapse isOpen={toggle} navbar>
          <Nav className="ml-auto" navbar>
            <NavbarBrand />
            <NavItem>
              <NavLink
                href={process.env.BACKEND_URL + "/"}
                className="nav-link"
              >
                Home
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href={process.env.BACKEND_URL + "/about"}>About</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href={process.env.BACKEND_URL + "/contactInfo"}>
                Contact
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href={process.env.BACKEND_URL + "/revelerProcess"}>
                Reveler Process
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href={process.env.BACKEND_URL + "/services"}>
                Services
              </NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </RMNavbarDiv>
  );
}

export default RMNavbar;
